<template>
    <div class="personal_tieiz_layer">
        <header class="login-header-bar clearfix">
            <a onclick="window.history.go(-1);" class="login-back-btn"></a>
            <h2 class="login-title"></h2>
            <a class="send-tiezi" @click="goWriteTiePage($event)"></a>
        </header>
      <scroller style="top: 0.4rem;"
                :on-refresh="refresh"
                :on-infinite="infinite"
                ref="my_scroller"

      >
        <div class="download-content-box clearfix personal-tiezi-box" id="personal-tiezi-box">
            <ul>
                <li class="tiezi-item" v-for="item in newDatas" v-show="item.status !==2 || item.status !=='2'">
                    <div class="line">

                    </div>
                    <div class="who-tiezi-item1 clearfix">
                        <img v-lazy="item.avatar" alt="" class="pc-tiezi-pic">
                        <div class="who-publish-box">
                            <p class="who-publish-name">
                                {{item.author}}
                            </p>
                            <div class="who-publish-times">
                                <span class="who-publish-time">
                                    {{item.created_at | filterTime}}
                                </span>
                                <span class="who-publish-tag">
                                    
                                    <span v-for="(tag,index) in item.tags">{{tag.name}}<span v-show="(index+1)!==item.tags.length"></span></span>
                                </span>
                            </div>
                        </div>
                        <a class="delete-tiezi" @click="delArticle($event,item.id)" :id="'item'+item.id">
                            
                        </a>
                    </div>
                    <div class="who-tiezi-item2">
                      <router-link  class="who-tiezi-info" :to="'/tiezidetail/'+item.id">
                            <p class="tiezi-title">
                              {{item.title}}
                            </p>
                            <p class="r-tiezi-content" v-html="item.body">
                                {{item.body}}
                            </p>

                      </router-link>
                    </div>
                    <div class="who-tiezi-item3 clearfix">
                        <div class="tiezi-read-box">
                            <i class="read-icon"></i>
                            <span class="tiezi-read-num">
                                {{item.article_views_count}}
                            </span>
                        </div>
                        <div class="tiezi-zanfh-box clearfix">
                            <div class="tiezi-hui-box">
                                <span class="tiezi-hui-icon">
                                </span>
                                <span class="tiezi-hui-text">
                                    {{item.article_comments_count}}
                                </span>
                            </div>
                            <div class="tiezi-zan-box">
                                <span class="tiezi-zan-icon">
                                </span>
                                <span class="tiezi-zan-text">
                                    {{item.article_likes_count}}
                                </span>
                            </div>
                        </div>
                    </div>
                </li>
            </ul>
        </div>
      </scroller>
    </div>
</template>
<script>
    import Api from '../../api/api';
    import { MessageBox } from 'mint-ui';
    import logUtil from '../../utils/log'
    export default{
        data(){
          return{
            //
            totalCount:0,
            //
            pageIndex:1,
            //
            pageSize:10,
            type:0,
            newDatas:[],
          }
        },
        mounted(){
            //vue-scroller
            this.$refs.my_scroller.finishInfinite(true);
//            console.log("mounted");
            this.getMineTieZiInfos(0,1);
        },
      filters:{
        filterTime(value){
          return value.split("T")[0];
        }
      },
        methods:{
            delArticle($event,id){
              const _this = this;
              MessageBox({
                title: '',
                message: '?',
                showCancelButton: true
              });
              MessageBox.confirm('?').then(action => {
                let token = localStorage.getItem('token');
                if(token !== null || token!==''){
                  //
                  _this.$http.put(`${Api.NEWSCOMMENTINFOS}/${id}/`).then((res)=>{
//                    console.log(":",res);
                    if(parseInt(res.status) === 204){
                      _this.showMsg('!');
                      _this.$router.go(0);
                    }
                  },(err)=>{
                    _this.showMsg('!');
                    _this.$store.dispatch('hideLoading');
                  })
                }else{
                  this.$router.push({path: '/login', replace: true});
                }
              });

            $event.preventDefault();
          },
            getMineTieZiInfos(type,page){
              const that = this;
              this.$http.get(`${Api.MYTIEZI}${localStorage.getItem('userId')}/topics/`,{
                params:{
                  type:0,
                  page:page,
                  page_size:that.pageSize,
                }
              }).then((res)=>{
                if(res.status === 200){
                  that.initTieZiData(type,res);
                }else{
                  that.dealErrInfo();
                }
              },(err)=>{
                that.dealErrInfo();
              });
            },
            dealErrInfo(){
              this.$store.dispatch('hideLoading');
              this.showMsg('');
            },
            initTieZiData(type,res){
              const that = this;
              that.totalCount = res.data.data.total;
              logUtil.printLog(":",res);
              if(res.data!==null && res.data.data.detail.length>0){
                let tempArr = [];
                tempArr = res.data.data.detail;
                if(type===0){
                  that.newDatas = tempArr;
                }
                //
                else if(type===1){
                  that.newDatas=tempArr.splice(0,that.pageSize);
                }else if(type === 2){ //
                  that.newDatas = that.newDatas.concat(tempArr);
                }
              }else{
                that.showMsg('!');
              }
            },
            showMsg(str){
              this.$toast({
                message: str,
                duration: 2000,
              });
            },
            goTieZiDetail($event){
                this.$router.push({path: '/tiezidetail', replace: true});
                $event.preventDefault();
            },
            goWriteTiePage($event){
              this.$router.push({path: '/writetie', replace: true});
              $event.preventDefault();
            },
            infinite(done){
              const that = this;
              that.pageIndex++;
              that.type = 2;
              setTimeout(() => {
                //load more over
                if(that.pageIndex>that.totalCount){
                  done(true);
                }else{
                  done();
                  that.getMineTieZiInfos(that.type,that.pageIndex);
                }
              }, 1500)
            },
          /**
           * 
           * @param done
           */
            refresh(done){
              const that = this;
              that.pageIndex = 1;
              that.type = 1;
              setTimeout(() => {
                that.getMineTieZiInfos(that.type,that.pageIndex);
                done()
              }, 1500)
            },

        }
    }
</script>
<style scoped>
    @import "../../assets/css/pcentiezi.css";
    body {
        background-color: #EDEEF0;
    }
</style>



